import React from 'react';

const Card = ({name, email, id}) => {
return (
    <div className='tc bg-light-blue dib br4 pa3 ma2 grow bw2 shadow-5'>
        <div>
            <img alt="robotPhoto" src={`https://robohash.org/${id}?200x200`}></img>
        </div>
            <h2>{name}</h2>
            <p>{email}</p>
    </div>
    );
}

export default Card;